import React, {ReactFragment} from 'react';
import {
    Placeholder,
    Cell,
    RichCell,
    Avatar, Tabs, CardGrid, Card, Button, TabsItem, Textarea, Counter
} from "@vkontakte/vkui"
import Icon56LikeOutline from '@vkontakte/icons/dist/56/like_outline';
import Icon24UserOutgoing from '@vkontakte/icons/dist/24/user_outgoing';
import bridge from "@vkontakte/vk-bridge";

import {BACKEND} from "../func/func";

class Moder extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            mastersList: [],
            activeTab: 'masters',
            comments: [],
            mastersCount: 0,
            usersCount: 0
        };
    }

    componentDidMount() {
        fetch(BACKEND.moder)
            .then(res => res.json())
            .then(mastersList => {
                this.setState({mastersList: mastersList})
            });
        fetch(BACKEND.comment.moderation)
            .then(res => res.json())
            .then(comments => {
                console.log(comments);
                this.setState({comments: comments})
            });
    }

    good = (id, index) => {
        let mastersList = this.state.mastersList;
        if (index > -1) {
            mastersList.splice(index, 1);
        } else mastersList.splice(0, index);
        this.setState({mastersList: mastersList});
        let moderation = {
            id: id,
            status: true,
            reasons: []
        };
        fetch(BACKEND.moder, {
            method: 'POST', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, cors, *same-origin
            cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
            credentials: 'same-origin', // include, *same-origin, omit
            headers: {
                'Content-Type': 'application/json',
            },
            redirect: 'follow', // manual, *follow, error
            referrer: 'no-referrer', // no-referrer, *client
            body: JSON.stringify(moderation), // тип данных в body должен соответвовать значению заголовка "Content-Type"
        })
            .then(res => res.json())
            .catch(e=>console.log(e))
    };

    bad = (id, index) => {
        let mastersList = this.state.mastersList;
        if (index > -1) {
            mastersList.splice(index, 1);
        } else mastersList.splice(0, index);
        this.setState({mastersList: mastersList});
        let reasons = [];
        reasons.push(this.state.reason);
        let moderation = {
            id: id,
            status: false,
            reasons: reasons
        };
        fetch(BACKEND.moder, {
            method: 'POST', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, cors, *same-origin
            cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
            credentials: 'same-origin', // include, *same-origin, omit
            headers: {
                'Content-Type': 'application/json',
            },
            redirect: 'follow', // manual, *follow, error
            referrer: 'no-referrer', // no-referrer, *client
            body: JSON.stringify(moderation), // тип данных в body должен соответвовать значению заголовка "Content-Type"
        })
            .then(res => res.json())
            .catch(e=>console.log(e))
    };

    goodComment = () => {

    };

    badComment = () => {

    };

    renderComments() {
        if (this.state.comments.length === 0){
            return (
                <Placeholder
                    icon={<Icon56LikeOutline/>}
                    header="На модерации"
                >Пока никого нет
                </Placeholder>
            )
        }
        return this.state.comments.map((comment, index)=> {
            if (comment === null) return null
            return (
                <CardGrid key={index} style={{padding: 0}}>
                    <Card size="l" mode="shadow">
                        <RichCell
                            disabled
                            multiline
                            actions={
                                <React.Fragment>
                                    <Button onClick={()=>this.goodComment(comment[0]._id, index)}>Одобрить</Button>
                                    <Button onClick={()=>this.badComment(comment[0]._id, index)} mode="secondary">Отклонить</Button>
                                </React.Fragment>
                            }
                        >
                            {comment[0].body}
                        </RichCell>
                    </Card>
                </CardGrid>
            )
        })
    }

    renderMasters() {
        if(this.state.mastersList.length === 0){
            return (
                <Placeholder
                    icon={<Icon56LikeOutline/>}
                    header="На модерации"
                >Пока никого нет
                </Placeholder>
            )
        } else {
            return this.state.mastersList.map((master, index) => {
                return (
                    <CardGrid key={master.vkUid} style={{padding: 0}}>
                        <Card size="l" mode="shadow">
                            <RichCell
                                disabled
                                multiline
                                before={<Avatar onClick={() => this.props.openMaster(master._id)} src={master.avatarLink} size={70}/>}
                                caption={master.location.city.title}
                                actions={
                                    <React.Fragment>
                                        <Button onClick={()=>this.good(master._id, index)}>Одобрить</Button>
                                        {/*<Button onClick={()=>this.bad(master._id, index)} mode="secondary">Отклонить</Button>*/}
                                    </React.Fragment>
                                }
                            >
                                {master.firstname} {master.lastname}
                            </RichCell>
                        </Card>
                    </CardGrid>
                );
            })
        }
    };

    render(){
        return (
            <div>
                <Tabs>
                    <TabsItem
                        onClick={() => this.setState({ activeTab: 'masters' })}
                        selected={this.state.activeTab === 'masters'}
                    >
                        Мастера
                    </TabsItem>
                    <TabsItem
                        onClick={() => this.setState({ activeTab: 'comments' })}
                        selected={this.state.activeTab === 'comments'}
                    >
                        Комментарии
                    </TabsItem>
                </Tabs>
                {
                    this.state.activeTab === 'masters' ? this.renderMasters() : this.renderComments()
                }
            </div>
        );
    }
}

export default Moder;